	module.exports = {"data":{"markdownRemark":{"html":"<h2>Array Length</h2>\n<p><code>length</code> is a property of arrays in JavaScript that returns or sets the number of elements in a given array.</p>\n<p>The <code>length</code> property of an array can be returned like so.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code><span class=\"token keyword\">let</span> desserts <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token string\">\"Cake\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"Pie\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"Brownies\"</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>\nconsole<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span>desserts<span class=\"token punctuation\">.</span>length<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span> <span class=\"token comment\" spellcheck=\"true\">// 3</span>\n</code></pre>\n      </div>\n<p>The assignment operator, in conjunction with the <code>length</code> property, can be used to set then number of elements in an array like so.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code><span class=\"token keyword\">let</span> cars <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token string\">\"Saab\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"BMW\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"Volvo\"</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>\ncars<span class=\"token punctuation\">.</span>length <span class=\"token operator\">=</span> <span class=\"token number\">2</span><span class=\"token punctuation\">;</span>\nconsole<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span>cars<span class=\"token punctuation\">.</span>length<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span> <span class=\"token comment\" spellcheck=\"true\">// 2</span>\n</code></pre>\n      </div>\n<h4>More Information:</h4>\n<p><a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Array/length\">https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Array/length</a></p>","fields":{"slug":"/javascript/standard-objects/array/array-length/"},"frontmatter":{"title":"Array Length"}}},"pathContext":{"slug":"/javascript/standard-objects/array/array-length/"}}

